<template>
    <div class="form__counter">
        <button type="button" aria-label="Убрать один товар" @click.prevent="productAmountIn > 1 ? productAmountIn-- : productAmountIn">
            <svg width="12" height="12" fill="currentColor">
            <use xlink:href="#icon-minus"></use>
            </svg>
        </button>

        <input type="text" v-model.number="productAmountIn">

        <button type="button" aria-label="Добавить один товар" @click.prevent="productAmountIn++">
            <svg width="12" height="12" fill="currentColor">
            <use xlink:href="#icon-plus"></use>
            </svg>
        </button>
    </div>
</template>

<script>
    export default {
        props:['productAmount'],
        computed:{
            productAmountIn:{
                get(){
                    return this.productAmount
                },
                set(value){
                    this.$emit('update:productAmount', value)  
                }
            }
        }
    }
</script>